  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  font-family: "Poppins", sans-serif;
  text-decoration: none;
}

p {
  font-family: "Poppins", sans-serif;
}

img {
  max-width: 100%;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3, h4 {
  margin: 0;
  color: #000000;
  text-align: center;
}

h1 {
  font-size: 3.5rem;
  font-weight: 900;
}

