.footerBar {
  background-color: rgb(236, 240, 241);
  display: flex;
  flex-direction: column;
}
.footerBar__superior {
  background-color: #FFFFFF;
}
.footerBar__superior .superiorContent {
  background-color: rgb(236, 240, 241);
  border-top-right-radius: 8rem;
  width: 96%;
  text-align: center;
  padding: 2rem 5rem;
}
@media (min-width: 640px) {
  .footerBar__superior .superiorContent {
    display: flex;
    justify-content: space-between;
  }
}
.footerBar__superior .superiorContent img {
  width: 30rem;
  height: 12rem;
}
.footerBar__inferior {
  background-color: rgb(44, 62, 80);
  border-top-left-radius: 8rem;
  width: 90%;
  align-self: flex-end;
  padding: 0 5rem;
  position: relative;
}
.footerBar__inferiorContent {
  border-bottom: 1px solid rgb(162, 163, 163);
  padding-bottom: 3rem;
}
@media (min-width: 820px) {
  .footerBar__inferiorContent {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
}
.footerBar__nav img {
  max-width: 24rem;
}
.footerBar__nav nav {
  padding-left: 2rem;
  text-align: center;
}
@media (min-width: 640px) {
  .footerBar__nav nav {
    display: flex;
    justify-content: center;
  }
}
.footerBar__nav nav a {
  display: block;
  color: #f1c40f;
  font-size: 2.2rem;
  padding: 0rem 1rem;
  font-weight: 300;
  margin-bottom: 1rem;
}
@media (min-width: 820px) {
  .footerBar__nav nav a {
    margin-bottom: 0;
  }
}
.footerBar__suscripcion {
  padding-top: 1rem;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  gap: 1rem;
  width: 100%;
  max-width: 33rem;
  margin: 0 auto;
}
@media (min-width: 820px) {
  .footerBar__suscripcion {
    margin: 0;
  }
}
.footerBar__suscripcion .suscribir {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  width: 100%;
  max-width: 22rem;
}
.footerBar__suscripcion .suscribir .textSuscribir {
  color: #FFFFFF;
  font-size: 1.3rem;
  font-weight: 300;
}
.footerBar__suscripcion .suscribir .campoSuscribir {
  width: 100%;
  outline: none;
  font-size: 1.2rem;
  font-weight: 300;
  padding: 0.6rem 1.7rem;
  border: 3px solid #FFFFFF;
  border-radius: 1rem;
}
.footerBar__suscripcion .suscribir .campoSuscribir:focus {
  background-color: rgb(236, 240, 241);
}
.footerBar__politicas {
  text-align: center;
  padding: 1rem 0 7rem 0;
}
@media (min-width: 768px) {
  .footerBar__politicas {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.footerBar__politicas .politica {
  color: rgb(162, 163, 163);
  font-weight: 300;
  font-size: 1.1rem;
  padding-right: 1rem;
}
.footerBar__politicas .politica:nth-of-type(1) {
  border-right: 1px solid rgb(162, 163, 163);
}
.footerBar__politicas .politica:nth-of-type(2) {
  padding-left: 0.5rem;
  border-right: 1px solid rgb(162, 163, 163);
}
.footerBar__politicas .politica:nth-of-type(3) {
  padding-left: 0.5rem;
}
.footerBar__politicas .allright {
  color: rgb(162, 163, 163);
  font-weight: 300;
  font-size: 1rem;
}
.footerBar .logoGov {
  background-color: rgb(28, 47, 63);
  width: 25rem;
  text-align: center;
  padding: 1.5rem 1rem 1rem 1rem;
  border-top-left-radius: 4rem;
  position: absolute;
  right: 0;
  bottom: 0;
}

* {
  z-index: 100;
}

.contenido {
  font-family: "Roboto", sans-serif;
  background-color: #004b23;
  color: #ffffff;
  overflow-x: hidden;
  background-image: url("/build/img/PROFESIONALES/FONDO.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.header {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 10px 45px;
  background-image: url("/build/img/Banner menú 2.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 116px;
}

.header__logo {
  position: absolute;
  top: 0px;
  left: 36px;
}

.header__logo img {
  height: auto;
}

/* Estilos para el menú hamburguesa */
.header__hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
  position: absolute;
  top: 25px;
  right: 20px;
  z-index: 10001;
}

.header__hamburger span {
  width: 30px;
  height: 3px;
  background-color: #FFFFFF;
  margin: 4px 0;
  transition: all 0.3s ease;
}

/* Ocultar el menú de navegación en pantallas pequeñas */
@media (max-width: 768px) {
  .header__nav {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 250px;
    background-color: #288d5b;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 20px;
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
    z-index: 9999;
    transition: transform 0.3s ease-in-out;
    transform: translateX(100%);
  }
  /* Mostrar el botón menú hamburguesa */
  .header__hamburger {
    display: flex;
  }
  /* Mostrar menú de navegación cuando esté activo */
  .header__nav.active {
    transform: translateX(0);
    background-color: #288d5b;
    display: flex;
    align-items: end;
  }
  /* Ajuste de los enlaces dentro del menú */
  .header__nav a {
    margin: 20px 0;
    color: #004b23;
    font-size: 22px;
    text-decoration: none;
    text-align: center;
    width: 100%;
  }
  /* Ajustar tamaño y visibilidad de los íconos sociales */
  .header__social {
    justify-content: flex-start;
    padding-left: 20px;
    margin-top: 30px;
  }
  .header__social a {
    margin-right: 15px;
    font-size: 24px;
    color: #004b23;
  }
}
.header__logo h1 {
  font-size: 24px;
  font-weight: bold;
  margin-left: 10px;
}

.header__nav {
  display: flex;
  align-items: center;
}

.header__nav a {
  color: #ffffff;
  text-decoration: none;
  margin-left: 20px;
  font-size: 18px;
  margin-top: 0;
}

.btn-contacto {
  display: block;
  padding: 10px 20px;
  color: #004b23;
  font-weight: bold;
  border: none;
  border-radius: 5px;
