.divider {
  position: relative;
  width: 100%;
  height: 3px;
  background-color: #f1c40f;
  border: none;
  margin: 20px 0;
}

.divider::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  width: 12px;
  height: 12px;
  background-color: #f1c40f;
  border-radius: 50%;
}

.hero__image {
  position: relative;
}

.hero__image img {
  width: auto;
  height: 470px;
}

.abogados {
