.cards {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  margin-top: 50px;
  flex-wrap: wrap;
  position: relative;
  top: 119px;
}

.card {
  position: relative;
  width: 264px;
  height: auto;
  border-radius: 41px;
  margin: 37px;
  overflow: hidden;
  background-color: #fff;
  text-align: center;
  color: #004b23;
  border: 5px solid transparent;
  background-image: linear-gradient(#fff, #fff), linear-gradient(45deg, tomato, orange, #32cd32, #00ced1);
  background-origin: border-box;
  background-clip: content-box, border-box;
}

.card__icono {
  position: absolute;
  bottom: -20px;
  left: 50%; /* Lo mueve horizontalmente al centro */
  transform: translateX(-50%); /* Centra exactamente en el medio */
}

.card__icono img {
  width: 30px; /* Tamaño del ícono */
  height: 30px;
  object-fit: cover;
}

.card img {
  width: 241px;
  height: 165px;
  object-fit: cover;
  padding: 1.5rem;
  border-radius: 3.2rem;
}

.card__content {
  padding: 0 20px 50px 20px;
}

.card__content h3 {
  font-size: 16px;
  margin-bottom: 7px;
  text-align: left;
  font-weight: 500;
}

.card__content p {
  font-size: 11px;
  line-height: 1.2;
  text-align: left;
}

/* Puedes ajustar el degradado para cada card */
.card:nth-child(1) {
  background-image: linear-gradient(#fff, #fff), linear-gradient(45deg, #f2d934, #f2d934); /* Rojo-Naranja */
}

.card:nth-child(2) {
  background-image: linear-gradient(#fff, #fff), linear-gradient(45deg, #cfd9db, #33464b); /* Verde-Azul */
}

.card:nth-child(3) {
